.header {
  background: #fff;
  padding: 0 50px;

  &__logo {
    width: 40px;
    height: 40px;
    padding: 10px 0;
    margin: 0;
  }
}

.page__header {
  margin: 0 auto;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: 1200px) {
    & {
      max-width: 1200px;
    }
  }
}
